/**
 * Paycheck input component for creating pay periods.
 */

import React, { useState } from 'react';
import {
  Box,
  TextField,
  Button,
  Typography,
  Paper,
  Grid,
  Alert,
  InputAdornment,
  CircularProgress,
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { AttachMoney, CalendarToday } from '@mui/icons-material';
import { addDays, format } from 'date-fns';
import { createPayPeriod } from '../../services/budget.service';
import { PayPeriodCreate } from '../../types/budget';
import { formatCurrency } from '../../utils/formatters';

interface PaycheckInputProps {
  onPayPeriodCreated: (payPeriod: any) => void;
  currentPayPeriod?: any;
}

export const PaycheckInput: React.FC<PaycheckInputProps> = ({ onPayPeriodCreated }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [formData, setFormData] = useState({
    totalIncome: '',
    startDate: new Date(),
    endDate: addDays(new Date(), 13), // Default to 14-day period
  });

  const handleIncomeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    // Only allow numbers and decimal point
    if (value === '' || /^\d*\.?\d*$/.test(value)) {
      setFormData(prev => ({ ...prev, totalIncome: value }));
      setError(null);
    }
  };

  const handleStartDateChange = (newDate: Date | null) => {
    if (newDate) {
      setFormData(prev => ({
        ...prev,
        startDate: newDate,
        endDate: addDays(newDate, 13), // Auto-calculate end date
      }));
    }
  };

  const handleEndDateChange = (newDate: Date | null) => {
    if (newDate) {
      setFormData(prev => ({ ...prev, endDate: newDate }));
    }
  };

  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault();
    setError(null);
    
    if (!formData.totalIncome || parseFloat(formData.totalIncome) <= 0) {
      setError('Please enter a valid income amount');
      return;
    }

    if (formData.startDate >= formData.endDate) {
      setError('End date must be after start date');
      return;
    }

    setIsLoading(true);

    try {
      const payPeriodData: PayPeriodCreate = {
        start_date: format(formData.startDate, 'yyyy-MM-dd'),
        end_date: format(formData.endDate, 'yyyy-MM-dd'),
        total_income: formData.totalIncome,
      };

      const newPayPeriod = await createPayPeriod(payPeriodData);
      onPayPeriodCreated(newPayPeriod);
      
      // Reset form
      setFormData({
        totalIncome: '',
        startDate: new Date(),
        endDate: addDays(new Date(), 13),
      });
    } catch (err: any) {
      setError(err.message || 'Failed to create pay period');
    } finally {
      setIsLoading(false);
    }
  };

  const incomeValue = parseFloat(formData.totalIncome) || 0;

  return (
    <Paper elevation={2} sx={{ p: 3 }}>
      <Typography variant="h6" gutterBottom color="primary">
        New Pay Period
      </Typography>
      
      <Typography variant="body2" color="textSecondary" sx={{ mb: 3 }}>
        Create a new biweekly budget period
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}

      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <Box component="form" onSubmit={handleSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                label="Paycheck Amount"
                value={formData.totalIncome}
                onChange={handleIncomeChange}
                disabled={isLoading}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AttachMoney color="action" />
                    </InputAdornment>
                  ),
                }}
                helperText={incomeValue > 0 ? `Total: ${formatCurrency(incomeValue)}` : 'Enter your total biweekly income'}
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <DatePicker
                label="Start Date"
                value={formData.startDate}
                onChange={handleStartDateChange}
                disabled={isLoading}
                slotProps={{
                  textField: {
                    fullWidth: true,
                    required: true,
                    InputProps: {
                      startAdornment: (
                        <InputAdornment position="start">
                          <CalendarToday color="action" />
                        </InputAdornment>
                      ),
                    },
                  },
                }}
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <DatePicker
                label="End Date"
                value={formData.endDate}
                onChange={handleEndDateChange}
                disabled={isLoading}
                slotProps={{
                  textField: {
                    fullWidth: true,
                    required: true,
                  },
                }}
              />
            </Grid>
          </Grid>

          <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
            <Button
              type="submit"
              variant="contained"
              disabled={!formData.totalIncome || isLoading}
              sx={{ minWidth: 150 }}
            >
              {isLoading ? (
                <CircularProgress size={24} color="inherit" />
              ) : (
                'Create Pay Period'
              )}
            </Button>
          </Box>
        </Box>
      </LocalizationProvider>
    </Paper>
  );
};